.Button {
  user-select: none;
  -webkit-touch-callout: none;
  position: relative;
  overflow: hidden;
}

.Button.set {
  background: #ccc;
}

.Button:after {
  content: " ";
  background-color: #00ff0080;
  position: absolute;
  inset: 0;
  transform: translateX(-101%);
}

.Button.pushed:after {
  background-color: #ffff0080;
  transition: transform 1s ease-in, background-color .2s 1s;
  transform: translateX(0);
}

.Button > img {
  object-fit: contain;
  object-position: bottom;
  z-index: 0;
  filter: brightness(1.5) opacity(.3);
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}

.Button > span {
  z-index: 1;
  position: relative;
}

.Button.set > img {
  filter: brightness(1.5) opacity(.3) grayscale();
}

html, body {
  margin: 0;
  padding: 0;
}

main {
  box-sizing: border-box;
  flex-direction: column;
  gap: 1em;
  height: 100vh;
  padding: 1em;
  display: flex;
}

header {
  flex-grow: 0;
  justify-content: space-between;
  align-items: baseline;
  gap: 1em;
  display: flex;
}

h1 {
  margin: 0;
}

section {
  flex-grow: 1;
  flex-basis: 1%;
  gap: 1em;
  display: flex;
  position: relative;
}

section > :not(.overlay) {
  text-align: center;
  border: 1px solid #000;
  border-radius: .5em;
  flex-grow: 1;
  flex-basis: 1%;
}

section > .overlay {
  z-index: 1;
  background-color: #ffffffe0;
  justify-content: center;
  align-items: center;
  gap: 1ch;
  padding-left: 1em;
  font-size: 2em;
  display: flex;
  position: absolute;
  inset: 0;
}

section > .none {
  opacity: 0;
}

section > :not(.overlay).whodunit {
  border: thick solid red;
}

section.players .Button > img {
  object-fit: cover;
  object-position: top;
}
